.tableBlock {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.table_item {
  width: 100%;
  list-style: none;
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  background: #f2f2f2;
  transition: .5s all;
  &:hover {
    box-shadow: 4px 4px 17px 0px rgba(34, 60, 80, 0.2);
    cursor: pointer; } }

.table {
  margin-left: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 0;
  &_icon {
    display: flex;
    justify-content: center;
    width: 50px;
    align-items: center;
    i {
      font-size: 25px;
      width: 50px;
      color: #4388f7; } }
  h3 {
    display: flex;
    justify-content: center;
    //border: solid 2px red
    align-items: center;
    width: 25%; } }


.container {
  display: flex;
  justify-content: flex-start; }


.table_inf {
  padding: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around; }


.good {
  color: forestgreen; }

.bad {
  color: red; }
